import $ from 'jquery'
import 'jquery-validation'
import 'jquery-validation/dist/additional-methods'

function burgerMenu(selector) {
    let menu = $(selector);
    let button = menu.find('.burger-menu__button', '.burger-menu__lines');
    let links = menu.find('.burger-menu__link');
    let overlay = menu.find('.burger-menu__overlay');

    button.on('click', (e) => {
        console.log(button)
        e.preventDefault();
        toggleMenu();
    });



    links.on('click', () => toggleMenu());
    overlay.on('click', () => toggleMenu());

    function toggleMenu(){
        menu.toggleClass('burger-menu__active');

        if (menu.hasClass('burger-menu__active')) {
            $('body').css('overflow', 'hidden');
            $('header').css('position', 'sticky')
        } else {
            $('body').css('overflow', 'visible');
            $('header').css('position', 'relative')
        }
    }
}

burgerMenu('.burger-menu');

$(document).ready(function () {
    console.log(213)
    $("#membershipActive").validate({
        rules: {
            emailMembership: {
                email: true,
                required: true,

                highlight: function (element, errorClass, validClass) {
                    $(element).closest('#email-form').addClass(errorClass).removeClass(validClass);
                },
                unhighlight: function (element, errorClass) {
                    $(element).closest('#email-form').removeClass(errorClass);
                },
                success: function () {
                    $('#email-form').remove('invalid-input', 'error').addClass("success-input")
                },

            },
            newPasswordMembership: {
                required: true,
                minlength: 5,
                highlight: function (element, errorClass, validClass) {
                    $(element).closest('#new-password-form').addClass(errorClass).removeClass(validClass);
                },
                unhighlight: function (element, errorClass, validClass) {
                    $(element).closest('#new-password-form').addClass(validClass).removeClass(errorClass);
                },
                success: function () {
                    $('#new-password-form').addClass("success-input")
                },

            },
            passwordMembership: {
                required: true,
                minlength: 5,
                highlight: function (element, errorClass, validClass) {
                    $(element).closest('#password-form').addClass(errorClass).removeClass(validClass);
                },
                unhighlight: function (element, errorClass, validClass) {
                    $(element).closest('#password-form').addClass(validClass).removeClass(errorClass);
                },
                success: function () {
                    $('#password-form').addClass("success-input")
                },

            },
            confirmPasswordMembership: {
                required: true,
                minlength: 5,
                highlight: function (element, errorClass, validClass) {
                    $(element).closest('#confirm-password-form').addClass(errorClass).removeClass(validClass);
                },
                unhighlight: function (element, errorClass, validClass) {
                    $(element).closest('#confirm-password-form').addClass(validClass).removeClass(errorClass);
                },
                success: function () {
                    $('#confirm-password-form').addClass("success-input")
                },

            }
        },
        errorClass: 'invalid-input',
        validClass: 'success-input',
        messages: {
            email: {
                required: 'This field is required',
            },
            password: {
                required: 'This field is required',
                minlenght: 'Please enter at least 5 characters'
            },

        },
        submitHandler: function (form) {
            $(form).submit();
        }
    })
})

$(function () {
    $(document).on('input', '[data-field="item"]', function () {
        let item = $(this);
        console.log(item.val().length)
        if(item.val().length > 0) {
            $('.membership-active__form-group-password-img').css('display', 'none')
            $('.membership-active__form-group-password-img-cross-eye').css('display', 'block')
        } else if( item.val().length === 0){
            $('.membership-active__form-group-password-img').css('display', 'block')
            $('.membership-active__form-group-password-img-cross-eye').css('display', 'none')
        }
    });
});

