import '../../../styles/index.scss';
import 'bootstrap'
import 'bootstrap/dist/css/bootstrap.css';
import $ from "jquery";
import 'slick-carousel';
// import video_1 from '../../../media/videos/video.mp4';

$('.join__sites-block').slick({
    arrows: false,
    dots: false,
    autoplay: false,
    slidesToShow: 4,
    responsive: [{
        breakpoint:768,
        settings: {
            centerMode: false,
            slidesToShow: 1,
        }
    }]
});
function burgerMenu(selector) {
    let menu = $(selector);
    let button = menu.find('.burger-menu__button', '.burger-menu__lines');
    let links = menu.find('.burger-menu__link');
    let overlay = menu.find('.burger-menu__overlay');

    button.on('click', (e) => {
        console.log(button)
        e.preventDefault();
        toggleMenu();
    });


    links.on('click', () => toggleMenu());
    overlay.on('click', () => toggleMenu());

    function toggleMenu() {
        menu.toggleClass('burger-menu__active');

        if (menu.hasClass('burger-menu__active')) {
            $('body').css('overflow', 'hidden');
            $('header').css('position', 'sticky')
        } else {
            $('body').css('overflow', 'visible');
            $('header').css('position', 'relative')
        }
    }
}

burgerMenu('.burger-menu');


jQuery(document).ready(function($) {
    $('.main-releases__video-wrap').mouseenter(function () {
        $(this).children('.main-releases__item-img').removeClass('video-item-show').addClass('video-item-hide')
        $(this).children('.shadow').removeClass('main-releases__item-shadow')
        $(this).children('.main-releases__item-video').addClass('video-item-show').removeClass('video-item-hide')
    });
    $('.main-releases__video-wrap').on('mouseenter', function () {
        $(this).children('shadow').removeClass('main-releases__item-shadow')
        $(this).children('.main-releases__item-img').removeClass('video-item-show').addClass('video-item-hide')
        $(this).children('.main-releases__item-video').addClass('video-item-show').removeClass('video-item-hide')
    })
    $('.main-releases__video-wrap').mouseleave(function () {
        $(this).children('.main-releases__item-video').removeClass('video-item-show').addClass('video-item-hide')
        $(this).children('.shadow').addClass('main-releases__item-shadow')
        $(this).children('.main-releases__item-img').addClass('video-item-show').removeClass('video-item-hide')
    });
});
