import $ from 'jquery'

function burgerMenu(selector) {
    let menu = $(selector);
    let button = menu.find('.burger-menu__button', '.burger-menu__lines');
    let links = menu.find('.burger-menu__link');
    let overlay = menu.find('.burger-menu__overlay');

    button.on('click', (e) => {
        console.log(button)
        e.preventDefault();
        toggleMenu();
    });



    links.on('click', () => toggleMenu());
    overlay.on('click', () => toggleMenu());

    function toggleMenu(){
        menu.toggleClass('burger-menu__active');

        if (menu.hasClass('burger-menu__active')) {
            $('body').css('overflow', 'hidden');
            $('header').css('position', 'sticky')
        } else {
            $('body').css('overflow', 'visible');
            $('header').css('position', 'relative')
        }
    }
}

burgerMenu('.burger-menu');

jQuery(document).ready(function($) {
    $('.videos-releases__video-wrap').mouseenter(function () {
        $(this).children('.videos-releases__item-img').removeClass('video-item-show').addClass('video-item-hide')
        $(this).children('.shadow').removeClass('videos-releases__item-shadow')
        $(this).children('.videos-releases__item-video').addClass('video-item-show').removeClass('video-item-hide')
    });
    $('.videos-releases__video-wrap').on('mouseenter', function () {
        $(this).children('shadow').removeClass('videos-releases__item-shadow')
        $(this).children('.videos-releases__item-img').removeClass('video-item-show').addClass('video-item-hide')
        $(this).children('.videos-releases__item-video').addClass('video-item-show').removeClass('video-item-hide')
    })
    $('.videos-releases__video-wrap').mouseleave(function () {
        $(this).children('.videos-releases__item-video').removeClass('video-item-show').addClass('video-item-hide')
        $(this).children('.shadow').addClass('videos-releases__item-shadow')
        $(this).children('.videos-releases__item-img').addClass('video-item-show').removeClass('video-item-hide')
    });
});

$(".pagination-block").on("click", ".pagination__btn-small", function(){
    $(".pagination-block .pagination__btn-small").removeClass("pagination__btn-active"); //удаляем класс во всех вкладках
    $(this).addClass("pagination__btn-active"); //добавляем класс текущей (нажатой)
});

$(".pagination-mobile-pages").on("click", ".pagination-mobile__btn-small", function(){
    $(".pagination-mobile-pages .pagination-mobile__btn-small").removeClass("pagination-mobile__btn-active"); //удаляем класс во всех вкладках
    $(this).addClass("pagination-mobile__btn-active"); //добавляем класс текущей (нажатой)
});
