import $ from 'jquery'
import 'jquery-validation'
import 'jquery-validation/dist/additional-methods'

function burgerMenu(selector) {
    let menu = $(selector);
    let button = menu.find('.burger-menu__button', '.burger-menu__lines');
    let links = menu.find('.burger-menu__link');
    let overlay = menu.find('.burger-menu__overlay');

    button.on('click', (e) => {
        console.log(button)
        e.preventDefault();
        toggleMenu();
    });



    links.on('click', () => toggleMenu());
    overlay.on('click', () => toggleMenu());

    function toggleMenu(){
        menu.toggleClass('burger-menu__active');

        if (menu.hasClass('burger-menu__active')) {
            $('body').css('overflow', 'hidden');
            $('header').css('position', 'sticky')
        } else {
            $('body').css('overflow', 'visible');
            $('header').css('position', 'relative')
        }
    }
}

burgerMenu('.burger-menu');

$(function () {
    $(document).on('input', '[data-field="item"]', function () {
        let item = $(this);
        console.log(item.val().length)
        if(item.val().length > 0) {
            $('.payment__field-password-img').css('display', 'none')
            $('.payment__field-password-img-cross-eye').css('display', 'block')
        } else if( item.val().length === 0){
            $('.payment__field-password-img').css('display', 'block')
            $('.payment__field-password-img-cross-eye').css('display', 'none')
        }
    });
});

$(document).ready(function () {
    console.log(132)
    $("#paymentSignUp").validate({
        rules: {
            signUpEmail: {
                email: true,
                required: true,

                highlight: function (element, errorClass, validClass) {
                    $(element).closest('.payment__field-input-email').addClass(errorClass).removeClass(validClass);
                },
                unhighlight: function (element, errorClass) {
                    $(element).closest('.payment__field-input-email').removeClass(errorClass);
                },
                success: function () {
                    $('.payment__field-input-email').remove('invalid-input-sign-up', 'error-sign-up-sign-up').addClass("success-input-sign-up")
                },

            },
            signUpUsername: {
                required: true,
                minlength: 2,
                highlight: function (element, errorClass, validClass) {
                    $(element).closest('.login__form-password-username').addClass(errorClass).removeClass(validClass);
                },
                unhighlight: function (element, errorClass, validClass) {
                    $(element).closest('.login__form-password-username').addClass(validClass).removeClass(errorClass);
                },
                success: function () {
                    $('.login__form-password-username').addClass("success-input-sign-up")
                },

            },

            signUpPassword: {
                required: true,
                minlength: 5,
                highlight: function (element, errorClass, validClass) {
                    $(element).closest('.login__form-password-input').addClass(errorClass).removeClass(validClass);
                },
                unhighlight: function (element, errorClass, validClass) {
                    $(element).closest('.login__form-password-input').addClass(validClass).removeClass(errorClass);
                },
                success: function () {
                    $('.login__form-password-input').addClass("success-input-sign-up")
                },

            }
        },
        errorClass: 'invalid-input-sign-up',
        validClass: 'success-input-sign-up',
        messages: {
            email: {
                required: 'This field is required',
            },
            password: {
                required: 'This field is required',
                minlenght: 'Please enter at least 5 characters'
            },

        },
        submitHandler: function (form) {
            $(form).submit();
        }
    })
})
