import $ from 'jquery'

function burgerMenu(selector) {
    let menu = $(selector);
    let button = menu.find('.burger-menu__button', '.burger-menu__lines');
    let links = menu.find('.burger-menu__link');
    let overlay = menu.find('.burger-menu__overlay');

    button.on('click', (e) => {
        console.log(button)
        e.preventDefault();
        toggleMenu();
    });



    links.on('click', () => toggleMenu());
    overlay.on('click', () => toggleMenu());

    function toggleMenu(){
        menu.toggleClass('burger-menu__active');

        if (menu.hasClass('burger-menu__active')) {
            $('body').css('overflow', 'hidden');
            $('header').css('position', 'sticky')
        } else {
            $('body').css('overflow', 'visible');
            $('header').css('position', 'relative')
        }
    }
}

burgerMenu('.burger-menu');


jQuery(document).ready(function($) {
    $('.model__video-item-wrap').mouseenter(function () {
        $(this).children('.model__block-img').removeClass('video-item-show').addClass('video-item-hide')
        $(this).children('.shadow').removeClass('model__block-shadow')
        $(this).children('.model__block-video').addClass('video-item-show').removeClass('video-item-hide')
    });
    $('.model__video-item-wrap').on('mouseenter', function () {
        $(this).children('shadow').removeClass('model__block-shadow')
        $(this).children('.model__block-img').removeClass('video-item-show').addClass('video-item-hide')
        $(this).children('.model__block-video').addClass('video-item-show').removeClass('video-item-hide')
    })
    $('.model__video-item-wrap').mouseleave(function () {
        $(this).children('.model__block-video').removeClass('video-item-show').addClass('video-item-hide')
        $(this).children('.shadow').addClass('model__block-shadow')
        $(this).children('.model__block-img').addClass('video-item-show').removeClass('video-item-hide')
    });
});
