import $ from 'jquery'

function burgerMenu(selector) {
    let menu = $(selector);
    let button = menu.find('.burger-menu__button', '.burger-menu__lines');
    let links = menu.find('.burger-menu__link');
    let overlay = menu.find('.burger-menu__overlay');

    button.on('click', (e) => {
        console.log(button)
        e.preventDefault();
        toggleMenu();
    });



    links.on('click', () => toggleMenu());
    overlay.on('click', () => toggleMenu());

    function toggleMenu(){
        menu.toggleClass('burger-menu__active');

        if (menu.hasClass('burger-menu__active')) {
            $('body').css('overflow', 'hidden');
            $('header').css('position', 'sticky')
        } else {
            $('body').css('overflow', 'visible');
            $('header').css('position', 'relative')
        }
    }
}

burgerMenu('.burger-menu');

$(".pagination-models").on("click", ".pagination-models__btn-small", function(){
    $(".pagination-models .pagination-models__btn-small").removeClass("pagination-models__btn-active"); //удаляем класс во всех вкладках
    $(this).addClass("pagination-models__btn-active"); //добавляем класс текущей (нажатой)
});

$(".pagination-models-mobile-pages").on("click", ".pagination-models-mobile__btn-small", function(){
    $(".pagination-models-mobile-pages .pagination-models-mobile__btn-small").removeClass("pagination-models-mobile__btn-active"); //удаляем класс во всех вкладках
    $(this).addClass("pagination-models-mobile__btn-active"); //добавляем класс текущей (нажатой)
});
